<template>
    <div class="item" @click="release($event.currentTarget)">
      
        <div class="title">
            {{ this. title }}
        </div>
      
      <div class="description">
            {{ this.description }}
      </div>

    </div>
</template>

<script>

export default {

    name: 'Item',

    props: {
        title: String,
        description: String
    },

    methods: {
        release(target) {
            this.$emit('activated', 'jsonstream.herokuapp.com' + (target.children[0].innerHTML).trim());
        }
    }
}

</script>

<style lang="scss">

    .item {
        box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
        color: #ffe6e6;
        padding: 1em;
        border-radius: 10px;
        width: 300px;
        cursor: pointer;
        position: relative;
        transition: left 0.3s, box-shadow 0.3s, color 0.3s;
        text-align: center;
        left: 0;
        z-index: 100;

        &:hover {
            left: 2em;
            box-shadow: none;
            color: white;
        }

        .title {
            text-align: center;
        }

        .description {
            margin-top: .5rem;
            font-size: .70rem;
        }
    }

</style>
